import React, { useEffect } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export const CHAT_SPEECH_TYPE_REGEX = /(^|[^a-z])d?([^a-z]|$)/;
export const CHAT_SPEECH_TYPE_WARN = 'chat_speech';

const checkSpeechTypeWarning = (doc) => {
  const tmp = Object.entries(doc).filter(([k]) =>
    k === 'name'
    || k === 'description'
    || k === 'linkfb'
    || k === 'deadline'
    || k === 'benefit'
  ).map(([k, v]) => v).join('\n');
  const matched = tmp.match(CHAT_SPEECH_TYPE_REGEX);
  console.log(matched);
  return matched
    ? CHAT_SPEECH_TYPE_WARN
    : null;
};

export const checkWarning = (doc) => {
  let res = checkSpeechTypeWarning(doc);
  if (res) return res;
  return null;
};

export const FormConfirmation = ({ warn, onSubmit, clearWarn }) => {
  const [open, setOpen] = React.useState(false);

  useEffect(() => {
    if (warn) setOpen(true);
  }, [warn]);

  const handleClose = () => {
    setOpen(false);
    clearWarn();
  };

  const handleConfirm = () => {
    handleClose();
    onSubmit();
  };

  return <>
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">Xc nhn</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {warn === CHAT_SPEECH_TYPE_WARN && <>
            Trong phn bn in, bn c vit nhng t nh <b>"d"</b> hay <b>""</b>. Xin bn ch , phn mm s <b>copy-paste trc tip cu bn  nhp ln bi ng trn page</b>, vy nn nu bn c vit my cu nh vy, xa i gip Jiji nha!<br/>
            <br/>
            Cn nu bn chc l tt c u n (c th  Jiji nhm ln g , mnh ch l con mo thi m!), th bn hy bm nt "Xc nhn gi" di y
          </>}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleConfirm} color="primary" autoFocus>
          Xc nhn gi
        </Button>
        <Button onClick={handleClose} color="primary">
          Quay li
        </Button>
      </DialogActions>
    </Dialog>
  </>;
};